// Default Style Reset
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  outline: none;
}

// Variables
$color-primary: #1f6f8b;
$color-dark: #1c2b2d;
$color-light: #ffffff;

@font-face {
  font-family: 'Roboto';
  src: url('./Roboto-Regular.ttf');
}

// General Styles
body {
  font-family: 'Roboto', sans-serif;
  background-color: #eeeeee;

  ul {
    padding: 0;
    list-style: none;
  }

  p {
    margin: 5px 0;
  }

  h1 {
    margin-bottom: 0.25em;
    color: $color-primary;
    letter-spacing: 1px;
    font-size: 2.5em;
  }
  h2 {
    opacity: 0.9;
    font-weight: bold;
    color: $color-primary;
    font-weight: normal;
    font-size: 1.1em;
  }

  input {
    text-align: center;
    padding: 8px 6px 8px 10px;
    border-radius: 5px;
    border: 2px solid $color-light;
    outline: none;
    background-color: #ffffff;
    color: $color-dark;
    font-size: 14px;
    font-weight: bold;
    line-height: 20px;
    align-self: center;
    height: 38px;
  }
}

// App Component
.App {
  padding: 2% 5%;
  margin-top: 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

// CitySearch Component
.CitySearch {
  display: flex;
  flex-direction: column;
  max-width: 300px;
  margin: auto;
  position: relative;

  .city {
    width: 250px;
  }

  .show-suggestions {
    display: block;
  }

  .display-none {
    display: none;
  }

  .suggestions {
    border-radius: 4px;
    width: 250px;
    position: absolute;
    z-index: 2;
    align-self: center;
    margin: 0;
    top: 54px;
    font-size: 14px;
  }

  .suggestions > li {
    text-align: left;
    padding: 8px 6px 8px 10px;
    background-color: #dddddd;
    cursor: default;
  }

  .suggestions > li:last-child {
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
  }

  .suggestions > li:hover {
    background-color: $color-primary;
    color: $color-light;
  }
}

// NumberOfEvents Component
.NumberOfEvents {
  width: 200px;
  margin: 1em auto;
  margin-top: 0.5em;
  margin-bottom: 1em;
  text-align: center;
}

// EventList Component
.EventList {
  width: 100%;
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fit, minmax(300px, 700px));
  gap: 1em;
}

// Event Component
.Event {
  background-color: $color-light;
  color: $color-dark;
  padding: 1em;
  margin-bottom: 0.5em;
  text-align: left;
  border-radius: 5px;
  position: relative;
  box-shadow: 3px 2px 2px 2px rgba(0, 0, 0, 0.075);
  opacity: 0.92;

  .summary {
    opacity: 1;
    font-size: 1.5em;
    font-weight: bold;
    letter-spacing: 0.5px;
    word-spacing: 2px;
    color: $color-primary;
    margin-bottom: 0.5em;
  }

  .subheading {
    font-size: 1em;
    margin-bottom: 1em;
  }

  .time {
    font-weight: bold;
  }

  .description {
    margin-bottom: 3em;
  }

  .btn-details {
    font-family: 'Roboto', sans-serif;
    border: none;
    background-color: $color-primary;
    color: #ffffff;
    width: 100px;
    position: absolute;
    right: 1em;
    bottom: 1em;
    padding: 0.8em 0.4em;
    border-radius: 5px;
    outline: none;
    cursor: pointer;
    font-weight: bold;
  }

  .Expanded-Event {
    h4 {
      margin: 1em 0;
      color: $color-primary;
    }

    .link-container {
      margin-bottom: 1em;
    }

    a.link {
      color: $color-primary;
    }
  }
}
